'use client'
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Eye, EyeOff, User, Lock, Shield, AlertTriangle } from 'lucide-react';

export default function Page() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [mounted, setMounted] = useState(false);
  const router = useRouter();

  useEffect(() => {
    setMounted(true);
  }, []);

  const showAlert = (type, title, text, timer = 2000) => {
    if (mounted && typeof window !== 'undefined') {
      if (window.Swal) {
        window.Swal.fire({
          icon: type,
          title: title,
          text: text,
          showConfirmButton: false,
          timer: timer,
          timerProgressBar: true,
          background: '#212529',
          color: '#fff',
          customClass: {
            popup: 'border border-danger'
          }
        });
      } else {
        alert(`${title}: ${text}`);
      }
    }
  };

  const validateForm = () => {
    if (!username.trim()) {
      showAlert('warning', 'Validation Error', 'Please enter your username');
      return false;
    }
    if (!password.trim()) {
      showAlert('warning', 'Validation Error', 'Please enter your password');
      return false;
    }
    return true;
  };

  const handleLogin = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    setIsLoading(true);
    
    try {
      const res = await fetch('http://itdev.cmtc.ac.th:3000/api/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username: username.trim(),
          password: password
        }),
      });

      const data = await res.json();

      if (data.token) {
        if (mounted && typeof window !== 'undefined') {
          localStorage.setItem('token', data.token);
          localStorage.setItem('loginTime', new Date().toISOString());
        }
        
        showAlert('success', 'Login Successful!', 'Welcome back! Redirecting...', 2000);
        
        setTimeout(() => {
          router.push('/admin/users');
        }, 2000);
      } else {
        showAlert('error', 'Login Failed!', data.message || 'Invalid username or password', 3000);
      }
    } catch (error) {
      console.error('Login error:', error);
      showAlert('error', 'Connection Error', 'Unable to connect to server. Please try again.', 3000);
    } finally {
      setIsLoading(false);
    }
  };

  const handleNavigation = (url) => {
    router.push(url);
  };

  // Show loading spinner until component is mounted
  if (!mounted) {
    return (
      <div className="vh-100 d-flex justify-content-center align-items-center bg-dark">
        <div className="spinner-border text-danger" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }

  return (
    <div>
      <link
        href="https://cdnjs.cloudflare.com/ajax/libs/bootstrap/5.3.0/css/bootstrap.min.css"
        rel="stylesheet"
        integrity="sha512-t4GWSVZO1eC8BM339Xd7Uphw5s17a86tIZIj8qRxhnKub6WoyhnrxeCIMeAqBPgdZGlCcG2PrZjMc+Wr78+5Xg=="
        crossOrigin="anonymous"
      />
      <script
        src="https://cdnjs.cloudflare.com/ajax/libs/sweetalert2/11.10.1/sweetalert2.all.min.js"
        integrity="sha512-T2zUVJVYH0RFUuxgHu8OSKnLDm1bmOnS3h8iKw6lNHqJ6dqzWk6IzJfmKcrHIiD6jVLONKtbqfxfFHuCqVxY2w=="
        crossOrigin="anonymous"
      ></script>

      <div
        className="vh-100 d-flex align-items-center justify-content-center position-relative"
        style={{
          background: 'linear-gradient(135deg, #212529 0%, #6c1c1c 50%, #000000 100%)'
        }}
      >
        <div
          className="position-absolute top-0 start-0 w-100 h-100"
          style={{
            backgroundColor: 'rgba(0,0,0,0.3)',
            zIndex: 1
          }}
        ></div>

        <div className="container position-relative" style={{ zIndex: 2 }}>
          <div className="row justify-content-center">
            <div className="col-11 col-sm-8 col-md-6 col-lg-5 col-xl-4">
              <div
                className="card shadow-lg border-0 position-relative"
                style={{
                  background: 'linear-gradient(145deg, #343a40, #212529)',
                  border: '1px solid rgba(220, 53, 69, 0.3)',
                  borderRadius: '1rem',
                  zIndex: 2,
                  maxWidth: '100%'
                }}
              >
                <div
                  className="card-header text-center border-0 p-4 position-relative overflow-hidden"
                  style={{
                    background: 'linear-gradient(90deg, #dc3545, #c82333)',
                    borderRadius: '1rem 1rem 0 0'
                  }}
                >
                  <div
                    className="position-absolute top-0 start-0 w-100 h-100"
                    style={{ backgroundColor: 'rgba(0,0,0,0.2)' }}
                  ></div>
                  <div className="position-relative" style={{ zIndex: 1 }}>
                    <Shield className="mb-2 text-white" size={32} />
                    <h3 className="text-white mb-1">Secure Login</h3>
                    <small className="text-white-50">Welcome back! Please sign in to your account</small>
                  </div>
                </div>

                <div className="card-body p-4">
                  <div className="mb-3">
                    <label className="form-label text-light">Username</label>
                    <div className="input-group">
                      <span
                        className="input-group-text border-secondary"
                        style={{
                          backgroundColor: 'rgba(108, 117, 125, 0.3)',
                          borderColor: '#495057',
                          color: '#adb5bd'
                        }}
                      >
                        <User size={18} />
                      </span>
                      <input
                        type="text"
                        className="form-control border-secondary"
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                        placeholder="Enter your username"
                        style={{
                          backgroundColor: 'rgba(73, 80, 87, 0.5)',
                          borderColor: '#495057',
                          color: '#fff'
                        }}
                        onKeyPress={(e) => e.key === 'Enter' && handleLogin(e)}
                      />
                    </div>
                  </div>

                  <div className="mb-4">
                    <label className="form-label text-light">Password</label>
                    <div className="input-group">
                      <span
                        className="input-group-text border-secondary"
                        style={{
                          backgroundColor: 'rgba(108, 117, 125, 0.3)',
                          borderColor: '#495057',
                          color: '#adb5bd'
                        }}
                      >
                        <Lock size={18} />
                      </span>
                      <input
                        type={showPassword ? "text" : "password"}
                        className="form-control border-secondary"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        placeholder="Enter your password"
                        style={{
                          backgroundColor: 'rgba(73, 80, 87, 0.5)',
                          borderColor: '#495057',
                          color: '#fff'
                        }}
                        onKeyPress={(e) => e.key === 'Enter' && handleLogin(e)}
                      />
                      <button
                        type="button"
                        className="btn btn-outline-secondary border-secondary"
                        onClick={() => setShowPassword(!showPassword)}
                        style={{
                          backgroundColor: 'rgba(108, 117, 125, 0.3)',
                          borderColor: '#495057',
                          color: '#adb5bd'
                        }}
                      >
                        {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
                      </button>
                    </div>
                  </div>

                  <button
                    onClick={handleLogin}
                    disabled={isLoading}
                    className="btn w-100 py-3 fw-bold text-white"
                    style={{
                      background: isLoading ? '#6c757d' : 'linear-gradient(90deg, #dc3545, #c82333)',
                      border: 'none',
                      borderRadius: '0.5rem',
                      fontSize: '1.1rem',
                      boxShadow: '0 4px 15px rgba(220, 53, 69, 0.3)',
                      transition: 'all 0.3s ease'
                    }}
                    onMouseEnter={(e) => {
                      if (!isLoading) {
                        e.target.style.transform = 'translateY(-2px)';
                        e.target.style.boxShadow = '0 6px 20px rgba(220, 53, 69, 0.4)';
                      }
                    }}
                    onMouseLeave={(e) => {
                      if (!isLoading) {
                        e.target.style.transform = 'translateY(0)';
                        e.target.style.boxShadow = '0 4px 15px rgba(220, 53, 69, 0.3)';
                      }
                    }}
                  >
                    {isLoading ? (
                      <div className="d-flex align-items-center justify-content-center">
                        <div className="spinner-border spinner-border-sm me-2" role="status">
                          <span className="visually-hidden">Loading...</span>
                        </div>
                        Signing In...
                      </div>
                    ) : (
                      'Sign In'
                    )}
                  </button>

                  <div className="mt-3">
                    <div className="d-flex justify-content-between align-items-center">
                      <a
                        href="/Register"
                        className="text-decoration-none"
                        style={{ color: '#f8d7da' }}
                        onClick={(e) => {
                          e.preventDefault();
                          handleNavigation('/Register');
                        }}
                        onMouseEnter={(e) => e.target.style.color = '#dc3545'}
                        onMouseLeave={(e) => e.target.style.color = '#f8d7da'}
                      >
                        Create Account
                      </a>
                      <span className="text-muted">|</span>
                      <a
                        href="/"
                        className="text-decoration-none"
                        style={{ color: '#f8d7da' }}
                        onClick={(e) => {
                          e.preventDefault();
                          handleNavigation('/');
                        }}
                        onMouseEnter={(e) => e.target.style.color = '#dc3545'}
                        onMouseLeave={(e) => e.target.style.color = '#f8d7da'}
                      >
                        Forgot Password?
                      </a>
                    </div>
                  </div>
                </div>
              </div>

              <div
                className="card mt-3 border-0"
                style={{
                  background: 'rgba(52, 58, 64, 0.7)',
                  backdropFilter: 'blur(10px)'
                }}
              >
                <div className="card-body p-3">
                  <div className="d-flex align-items-center text-light">
                    <AlertTriangle className="me-2 text-warning" size={16} />
                    <small>Your connection is secured with end-to-end encryption</small>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}